export const SET_USER_AVATAR = 'SET_USER_AVATAR';
export const setUserAvatar = (avatar) => ({
  type: SET_USER_AVATAR,
  avatar,
})

export const SET_USER_NAME = 'SET_USER_NAME';
export const setUsername = (name) => ({
  type: SET_USER_NAME,
  name,
})



